<template>
  <div class="abilities bordered clear-bg">
    <ul class="abilities__container tree-view tree-view_with-links_extended">
      <li
        v-if="
          abilitiesList.generic.length !== 0 ||
          abilitiesList.defensive.length !== 0
        "
      >
        <b>Способности</b>
        <ul>
          <li
            v-for="(ability, index) in abilitiesList.generic"
            :key="`genericAbility${index}`"
            @click="expandAbility(ability)"
          >
            <i class="ra ra-blaster" /> {{ ability.display_name }}
          </li>
          <li class="no-link" v-if="abilitiesList.defensive.length > 0">
            <b>Защитные</b>
            <ul>
              <li
                v-for="(ability, index) in abilitiesList.defensive"
                :key="`defensiveAbility${index}`"
                @click="expandAbility(ability, true)"
              >
                <i class="ra ra-eye-shield" /> {{ ability.display_name }}
              </li>
            </ul>
          </li>
        </ul>
      </li>
      <li v-if="generalBuffsDisplayList.length > 0">
        <details>
          <summary>
            <b>Генерализованные баффы ({{ generalBuffsDisplayList.length }})</b>
          </summary>
          <ul class="no-links buffs">
            <li
              v-for="(buff, index) in generalBuffsDisplayList"
              :key="`buff${index}`"
            >
              <span>{{ buff.value }}</span> <span>{{ buff.key }}</span>
            </li>
          </ul>
        </details>
      </li>
      <li v-else class="abilities-empty">
        <i class="ra ra-carrot ra-4x" />
      </li>
    </ul>
  </div>
</template>

<script>
import generalBuffs from "@/enums/generalBuffs";

export default {
  name: "Abilities",
  data() {
    return {
      generalBuffs,
    };
  },
  props: {
    abilitiesModel: {
      type: Object,
    },
    generalBuffsModel: {
      type: Object,
    },
    preExpanded: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    abilitiesList() {
      return {
        generic:
          this.abilitiesModel.abilities_list?.map((item) => item.ability) || [],
        defensive:
          this.abilitiesModel.def_abilities_list?.map((item) => item.ability) ||
          [],
      };
    },
    generalBuffsDisplayList() {
      if (!this.generalBuffsModel) return [];
      return Object.entries(this.generalBuffsModel)
        .filter((item) => item[1] !== 0)
        .map((item) => {
          return {
            key: generalBuffs[item[0]] || item[0],
            value: item[1],
          };
        });
    },
  },
  methods: {
    expandAbility(ability, isDefensive = false) {
      this.$store.commit("addPopup", {
        component: "AbilityPopup",
        title: ability.display_name,
        params: {
          id: ability._id,
          isDefensive,
        },
      });
    },
  },
};
</script>

<style lang="scss">
.abilities {
  text-align: left;
  position: relative;
  flex: 2;

  &__container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    padding: 10px;
    text-align: left;
  }

  .buffs {
    li {
      display: flex;
      gap: 5px;
      align-items: center;
    }
    span:first-child {
      font-weight: bold;
      font-family: "Press Start 2P", cursive;
      font-size: 12px;
      width: 40px;
      min-width: 40px;
      justify-content: center;
      border: 2px dotted;
      padding: 2px;
      display: flex;
      align-items: center;
    }
  }
  .abilities-empty {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
  }
}
</style>
