<template>
  <div class="panel bordered attributes-buffs">
    <template v-if="attributesModel && filteredAttributes.length">
      <div class="title">Атрибуты</div>
      <div class="attribute-buff-container">
        <div
          v-for="(item, index) in filteredAttributes"
          :key="'att' + index"
          :title="item.name"
          class="attribute-buff"
        >
          <i class="ra" :class="item.icon" />
          <span :title="item.name">+{{ item.value }}</span>
        </div>
      </div>
    </template>
    <template v-if="skillsModel && filteredSkills.length">
      <div class="title mt-3">Навыки</div>
      <div class="skill-buff-container">
        <div
          class="skill-buff"
          v-for="(item, index) in filteredSkills"
          :key="'skill' + index"
        >
          {{ item.skill }}: <span>+{{ item.value }}</span>
        </div>
      </div>
    </template>
    <TerminalBlock
      v-if="!this.filteredAttributes.length && !this.filteredSkills.length"
      style="height: 100%; width: 100%"
    />
  </div>
</template>

<script>
import skillsEnum from "@/enums/skillsEnum";
import TerminalBlock from "@/components/TerminalBlock.vue";

const attributesEnum = {
  STRENGTH: {
    name: "Сила",
    icon: "ra-muscle-up",
  },
  AGILITY: {
    name: "Ловкость",
    icon: "ra-player-dodge",
  },
  ENDURANCE: {
    name: "Выносливость",
    icon: "ra-player-shot",
  },
  PERCEPTION: {
    name: "Восприятие",
    icon: "ra-aware",
  },
  INTELLIGENCE: {
    name: "Интеллект",
    icon: "ra-player-king",
  },
  DETERMINATION: {
    name: "Решимость",
    icon: "ra-player-pyromaniac",
  },
};
export default {
  name: "SkillsAndAttributesBuffs",
  components: { TerminalBlock },
  props: {
    attributesModel: {
      type: Object,
    },
    skillsModel: {
      type: Object,
    },
  },
  computed: {
    filteredAttributes() {
      if (!this.attributesModel) return [];
      return Object.entries(this.attributesModel)
        .filter((item) => item[1] > 0)
        .map(([key, value]) => {
          const result = attributesEnum[key];
          result.value = value;
          return result;
        });
    },
    filteredSkills() {
      if (!this.skillsModel) return [];
      return Object.entries(this.skillsModel)
        .filter((item) => item[1] > 0)
        .map(([key, value]) => ({
          skill: skillsEnum[key],
          value,
        }));
    },
  },
};
</script>

<style lang="scss">
.attributes-buffs {
  padding: 10px;
  height: 100%;
  box-sizing: border-box;

  > .title {
    font-family: "Press Start 2P", cursive;
    margin-bottom: 10px;
  }
  > .attribute-buff-container {
    display: flex;
    flex-wrap: wrap;

    .attribute-buff {
      font-weight: bold;
      font-family: "Press Start 2P", cursive;
      border-radius: 25px;
      width: 45px;
      min-width: 45px;
      height: 45px;
      border: 3px ridge;
      margin-right: 8px;
      background-color: white;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: zoom-in;
      .ra {
        font-size: 30px;
        color: silver;
        position: absolute;
      }
      span {
        position: relative;
      }
    }
  }
  .skill-buff-container {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    .skill-buff {
      font-family: "Roboto", sans-serif;
      border: 2px dotted;
      padding: 3px;
      display: flex;
      align-items: center;

      span {
        font-family: "Press Start 2P", cursive;
      }
    }
  }
}
</style>
