<template>
  <div
    class="trinket-popup popup-regular"
    :style="!isLoaded && 'cursor: progress'"
  >
    <template v-if="isLoaded">
      <div class="title row align-items-center">
        <div class="d-flex align-items-center col">
          <i class="ra ra-3x me-1" :class="itemTypeConfig.icon" />
          <span>
            {{ itemTypeConfig.humanReadableName }}:
            {{ itemModel.display_name }}
          </span>
        </div>
        <div class="col-2">
          <div class="ars-name bordered clear-bg">
            <div>{{ itemArs.name }}</div>
          </div>
        </div>
      </div>
      <div class="item-specific-section mt-2">
        <component
          :is="itemTypeConfig.specificComponent"
          :itemModel="itemModel"
          :popupData="popupData"
        />
      </div>
      <div class="row mt-2">
        <div class="col-4">
          <Requirements
            :attributes="itemModel?.attr_req"
            :skills="itemModel?.skill_req"
            :tags="itemModel?.tag_req"
            :stop-tags="itemModel?.tag_stop"
            :extra-requirements="extraRequirements"
          />
        </div>
        <div class="col">
          <div class="description bordered clear-bg">
            <p><b>Тэги:</b> /// в разработке</p>
            <p><b>ОД для надевания:</b> {{ itemModel.equip_od }}</p>
            {{ itemModel.description }}
          </div>
        </div>
        <div class="col-3"><Resists :resistsModel="itemModel.resists" /></div>
      </div>
    </template>
  </div>
</template>

<script>
import arses from "@/enums/arses";
import requestConfigs from "@/api/requestConfigs";
import { createRequest } from "@/api/requestBuilder";
import Requirements from "@/components/Statblocks/Requirements.vue";
import Resists from "@/components/Statblocks/Resists.vue";
import TrinketBlock from "@/components/PopupSections/Popups/ItemPopup/TrinketBlock.vue";
import WearableBlock from "@/components/PopupSections/Popups/ItemPopup/WearableBlock.vue";

const itemTypeConfigs = {
  trinket: {
    requestConfigName: "GETTrinket",
    requestFieldName: "trinketName",
    requestUnwrapPath: "trinket",
    humanReadableName: "Тринкет",
    icon: "ra-ankh",
    specificComponent: TrinketBlock,
  },
  wearable: {
    requestConfigName: "GETWearable",
    requestFieldName: "wearableName",
    requestUnwrapPath: "ability",
    humanReadableName: "Доспех",
    icon: "ra-helmet",
    specificComponent: WearableBlock,
  },
};
export default {
  name: "ItemPopup",
  components: { TrinketBlock, Resists, Requirements },
  data() {
    return {
      itemModel: {},
      isLoaded: false,
    };
  },
  props: {
    popupData: {
      type: Object,
    },
  },
  computed: {
    itemTypeConfig() {
      return itemTypeConfigs[this.popupData.params.itemType];
    },
    itemArs() {
      return arses[this.itemModel.ars];
    },
    arsGradient() {
      if (!this.itemArs) return;
      return `linear-gradient(90deg, ${this.itemArs.colors[0]} 0%, ${this.itemArs.colors[1]} 50%, rgba(255, 255, 255, 0) 100%)`;
    },
    rarityShadow() {
      if (!this.itemArs) return;
      const color = {
        1: "#6c6868",
        2: "#00ff00",
        3: "#0000ff",
        4: "#ff00ff",
        5: "#ffff00",
      }[this.itemModel.rarity];
      return `${color} -3px 5px 2px`;
    },
    extraRequirements() {
      switch (this.popupData.params.itemType) {
        case "wearable":
          return {
            name: "Конечность",
            requirementsArray: this.itemModel.req_bodypart_tag,
          };
        default:
          return undefined;
      }
    },
  },
  beforeMount() {
    createRequest(requestConfigs[this.itemTypeConfig.requestConfigName], {
      queryPayload: {
        characterName: this.$store.state.selectedCharacterName,
        [this.itemTypeConfig.requestFieldName]: this.popupData.params.id,
      },
    }).then((response) => {
      this.itemModel = response.data[this.itemTypeConfig.requestUnwrapPath];
      this.isLoaded = true;
    });
  },
};
</script>

<style lang="scss">
.trinket-popup {
  position: relative;
  > .title {
    background: v-bind(arsGradient);
    padding: 5px;
    .ra {
      text-shadow: v-bind(rarityShadow);
    }
    span {
      font-family: "Press Start 2P", cursive;
      font-weight: bold;
      font-size: 20px;
    }
    .ars-name {
      font-family: "Press Start 2P", cursive;
      font-size: 12px;
      text-align: center;
      padding: 5px;
    }
  }
  .description {
    font-family: "Roboto", sans-serif;
    padding: 5px;
    height: calc(100% - 10px);

    p {
      margin-top: 0;
      margin-bottom: 8px;
    }
  }
}
</style>
