<template>
  <div class="resists bordered clear-bg">
    <b><i class="ra ra-broken-shield" /> Резисты</b>
    <div class="resists__container">
      <div
        class="resists__item"
        v-for="(resist, index) in resistsToShow"
        :key="'resist' + index"
      >
        <b>{{ resist.transcription }}:</b> {{ resist.value }}
      </div>
    </div>
  </div>
</template>

<script>
const resistsTranscription = {
  BURSTING: "Взрыв",
  CORRUPTIVE: "Порча",
  DIMENSIONAL: "Пространство",
  ENERGY: "Энергия",
  KINETIC: "Кинетика",
  OCEANIC: "Океан",
  PIERCING: "Прокалывание",
  PSIONIC: "Псионика",
  THERMAL: "Температура",
  TOXIC: "Токсины",
  UNSTOPPABLE: "Неостановимость",
  WITHERING: "Истощение",
};
export default {
  name: "Resists",
  props: {
    resistsModel: {
      type: Object,
    },
  },
  computed: {
    resistsToShow() {
      return Object.entries(this.resistsModel).map(([name, value]) => {
        return {
          transcription: resistsTranscription[name],
          value,
        };
      });
    },
  },
};
</script>

<style lang="scss">
.resists {
  font-family: "Roboto", sans-serif;
  padding: 5px;
  height: calc(100% - 10px);
  line-height: 1.4;

  &__container {
    margin-left: 10px;
    margin-top: 5px;
  }
}
</style>
